import { jwtDecode } from "jwt-decode";

const useUserRole = () => {
  const token = localStorage.getItem("token");

  if (!token) {
    return null; //   ,  null
  }

  try {
    const decoded = jwtDecode(token); //  
    return decoded.role; //   
  } catch (error) {
    console.error("  :", error);
    return null; //     null
  }
};

export default useUserRole;
